import { ChangeDetectionStrategy, ChangeDetectorRef, Component, DoCheck, OnInit } from "@angular/core";
import { User } from "./model/user";
import { UserStoreService } from "./store/services/user-store.service";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.less"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AppComponent implements OnInit, DoCheck {
  constructor(private userStore: UserStoreService,
              private cdr: ChangeDetectorRef) {
  }

  user: User | {};
  durachok: boolean = false;
  showSign: boolean;
  darkTheme: boolean;

  logout(): void {
    this.userStore.logout();
    this.cdr.markForCheck();
  }

  ngOnInit(): void {
    const currentHour = new Date().getHours();
    (currentHour >= 21 && currentHour < 24) || (0 <= currentHour && currentHour <= 7)
      ? this.darkTheme = true
      : this.darkTheme = false;
  }

  ngDoCheck(): void {
    this.userStore.loadUserInfo().subscribe(
      user => {
        this.user = user;
        if (Object.keys(user).length) {
          const userInfo = <User>user;
          if (userInfo.login.toLowerCase() === "huy") {
            this.durachok = true;
          }
          this.showSign = false;
        } else {
          this.showSign = true;
        }
        this.cdr.markForCheck();
      },
    );
  }
}
